.UnidadesCargando-container{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
    border-radius: 10px;
  }
  .UnidadesCargando-CheckIcon .UnidadesCargando-borrarIcon{
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    border-radius: 50%;
      }
      .UnidadesCargando-CheckIcon{
        position: absolute;
    left: 12px;
      }
      .UnidadesCargando-borrarIcon{
        position: absolute;
    top: -24px;
    right: 0;
      }
    .UnidadesCargando-text{
     margin: 24px 0 24px 24px;
     width: calc(100% - 120px);
     font-size: 18px;
     line-height: 24px;
     font-weight: 400;
      }
    .UnidadesCargando-container, .UnidadesCargando-CheckIcon, .UnidadesCargando-borrarIcon{
        background: linear-gradient(90deg, rgb(68, 66, 66), rgb(200,199,199));
        background-size: 400% 400%;
        animation: carga 3s ease-in-out infinite ;
      }
  
  @keyframes carga{
    0%{
      background-position: 0% 50%;
      }
    50%{
      background-position: 100% 50%;
    }
    100%{
        background-position: 0% 50%;
    }
  }
